/* PremiumButton.module.css */

.dark {
    background-color: #333;
    color: #fff;
    
  }
  
  .light {
    background-color: #fff;
    color: #333;
  }
  
  .activatePremium {
    background-color: #007bff;
    color: #fff;
    border: none;
    padding: 10px 20px;
    margin-right: 10px;
    cursor: pointer;
  }
  
  .toggleTheme,
  .downloadExpenses {
    background-color: #ccc;
    color: #333;
    border: none;
    padding: 10px 20px;
    margin-right: 10px;
    cursor: pointer;
  }
  
  .toggleTheme:hover,
  .downloadExpenses:hover {
    background-color: #999;
  }
  